@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #D2DBDC;
  margin: 0;
  font-family: 'Segoe UI', Helvetica Neue, Helvetica, Lucida Grande, Arial;
}

.app-window {
  display: flex;
  height: 100vh;
  background-color: #EDEDED;
}

.sidebar {
  width: 35%;
  max-width: 415px;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #DDD;
}

header {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
}

.header--avatar {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  cursor: pointer;
}

.header--btn {
  display: flex;
}

.header--btn--area {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.search {
  background-color: #f6f6f6;
  border-bottom: 1px solid #EEE;
  padding: 5px 15px;

}

.search--input {
  background-color: #fff;
  height: 40px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  padding: 0 10px;
}

.search--input input {
  flex: 1 1;
  border: 0;
  outline: 0;
  background-color: transparent;
  margin-left: 10px;
}

.chatlist {
  flex: 1 1;
  background-color: #fff;
  overflow-y: auto;
}

.chatlist::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.chatlist::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

.content-area {
  flex: 1 1;
}
.chatListItem {
    display: flex;
    cursor: pointer;
    align-items: center;
    height: 70px;
}

.chatListItem:hover {
    background-color: #f5f5f5;
}

.chatListItem.active {
    background-color: #EBEBEB;
}

.chatListItem--avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 15px;
}

.chatListItem--lines {
    height: 100%;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 1px solid #eee;
    padding-right: 15px;
    margin-left: 15px;
    flex-wrap: wrap;
    min-width: 0;
}

.chatListItem--line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.chatListItem--name {
    font-size: 17px;
    color: #000;
}

.chatListItem--date {
    font-size: 12px;
    color: #999;
}

.chatListItem--lastMsg {
    font-size: 14px;
    color: #999;
    display: flex;
    width: 100%;
}

.chatListItem--lastMsg p {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 0;
}
.chatIntro {
    background-color: #f8f9fa;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    border-bottom: 6px solid #4adf83;
}

.chatIntro img {
    width: 500px;
    height: auto;
}

.chatIntro h1 {
    font-size: 32px;
    color: #525252;
    font-weight: normal;
    margin-top: 30px;
}

.chatIntro h2 {
    font-size: 14px;
    color: #777;
    font-weight: normal;
    margin-top: 20px;
    line-height: 20px;
    text-align: center;
}
.chatWindow {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.chatWindow--header {
    height: 60px;
    border-bottom: 1px solid #CCC;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.chatWindow--header--info {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.chatWindow--avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 0 15px;
}

.chatWindow--name {
    font-size: 17px;
    color: #000;
}

.chatWindow--header--btn {
    display: flex;
    align-items: center;
    margin-right: 15px;
}

.chatWindow--btn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    overflow: hidden;
    transition: all ease 0.3s;
}

.chatWindow--body {
    flex: 1 1;
    overflow-y: auto;
    background-color: #e5ddd5;
    background-size: cover;
    background-position: center;
    background-image: url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png');
    padding: 20px 30px;
}

.chatWindow--body::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}

.chatWindow--body::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2);
}


.chatWindow--footer {
    height: 62px;
    display: flex;
    align-items: center;
}

.chatWindow--pre {
    display: flex;
    margin: 0 15px;
}

.chatWindow--input--area {
    flex: 1 1;
}

.chatWindow--input {
    width: 100%;
    height: 40px;
    border: 0;
    outline: 0;
    background-color: #fff;
    border-radius: 20px;
    font-size: 15px;
    color: #4a4a4a;
    padding-left: 15px;
}

.chatWindow--pos {
    display: flex;
    margin: 0 15px;
}

.chatWindow--emoji-area {
    height: 200px;
    overflow-y: hidden;
    transition: all ease 0.3s;
}

aside.emoji-picker-react {
    width: auto;
    background: none;
}

.emoji-picer-react .emoji-group::before {
    background: none;
}
.messageLine {
    margin-bottom: 10px;
    display: flex;
}

.messageItem {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 1px 1px #ccc;
    display: flex;
    flex-direction: column;
    padding: 3px;
    max-width: 90%;
}

.messageText {
    font-size: 14px;
    margin: 5px 40px 5px 5px;
}

.messageDate {
    font-size: 11px;
    color: #999;
    margin-right: 1px;
    text-align: right;
    height: 15px;
    margin-top: -15px;
}
.newChat {
    width: 35%;
    max-width: 415px;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    border-right: 1px solid #ddd;
}

.newChat--head {
    display: flex;
    background-color: #00bfa5;
    align-items: center;
    padding: 60px 15px 15px 15px;
}

.newChat--backbutton {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.newChat--headtitle {
    font-size: 19px;
    height: 40px;
    line-height: 40px;
    flex: 1 1;
    font-weight: bold;
    color: #fff;
    margin-left: 5px;
}

.newChat--list {
    flex: 1 1;
    overflow-y: auto;
}

.newChat--list::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}

.newChat--list::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2);
}

.newChat--item {
    display: flex;
    align-items: center;
    padding: 15px;
    cursor: pointer;
}

.newChat--item:hover {
    background-color: #f5f5f5;
}

.newChat--item--avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 15px;
}

.newChat--item--name {
    font-size: 17px;
    color: #000;
}
.login {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgb(60, 61, 61);
}

.login--fieldset {
    border: none;
    border-radius: 25px;
    background-color: transparent;
    display: flex;
    padding: 1rem 1rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.login--h1 {
    font-family: Roboto;
    font-size: 30px;
    color: rgb(255, 255, 255);
    font-weight: 100;
}

.login--btn {
    cursor: pointer;
    background-color: #0b7c6d;
    padding: 1.2rem;
    border-radius: 10px;
    font-size: 18px;
    border: none;
    outline: none;
    color: white;
    display: flex;
    align-items: center;
}

.login--btn:hover {
    cursor: pointer;
    background-color: #00584d91;
    transition: 1s;
}

.logo-google {
    margin: 0rem 0.5rem 0rem 0rem;
}

footer {
    justify-content: center;
    align-items: center;
    text-align: center;
    position: absolute;
    bottom: 0;
    width: 100%;
    font-size: 14px;
    color: #fff;
    background-color: #00000046;
}

footer a {
    text-decoration: none;
    color: #c0c0c0;
}

footer a:hover {
    text-decoration: underline;
    color: #a0f0bf;
}

@media(max-width: 768px) {
    footer {
        font-size: 0.8rem;
    }

    .loading img {
        margin-left: 10px;
    }
}
